<template>
	<view class="content">
		<view class="card">
			<view class="card__shadow">

			</view>
		</view>
		<view class="chart">
			<view class="charts-box">
				<qiun-data-charts type="ring" :opts="opts" :chartData="chartData" />
			</view>
			<view class="charts-total">
				
			</view>
		</view>
		<view class="card-list">

		</view>
	</view>

</template>
<script>
	export default {
		data() {
			return {
				chartData: {},
				//您可以通过修改 config-ucharts.js 文件中下标为 ['ring'] 的节点来配置全局默认参数，如都是默认参数，此处可以不传 opts 。实际应用过程中 opts 只需传入与全局默认参数中不一致的【某一个属性】即可实现同类型的图表显示不同的样式，达到页面简洁的需求。
				opts: {
					rotate: false,
					rotateLock: false,
					color: ["rgba(255, 114, 69, 1)"],
					padding: [0],
					dataLabel: false,
					legend: {
						show: false,
						position: "right",
						lineHeight: 25
					},
					title: {
						show:false,
						name: "收益率",
						fontSize: 12,
						color: "#666666"
					},
					subtitle: {
						name: "70%",
						fontSize: 12,
						color: "#7cb5ec"
					},
					extra: {
						ring: {
							ringWidth: 10,
							activeOpacity: 0.5,
							activeRadius: 10,
							offsetAngle: 0,
							labelWidth: 15,
							border: true,
							borderWidth: 3,
							borderColor: "#FFFFFF"
						}
					}
				}
			};
		},
		onReady() {
			this.getServerData();
		},
		methods: {
			getServerData() {
				//模拟从服务器获取数据时的延时
				setTimeout(() => {
					//模拟服务器返回数据，如果数据格式和标准格式不同，需自行按下面的格式拼接
					let res = {
						series: [{
							data: [{
								"name": "一班",
								"value": 50
							}]
						}]
					};
					this.chartData = JSON.parse(JSON.stringify(res));
				}, 500);
			},
		}
	}
</script>
<style lang="scss" scoped>
	.card {
		position: relative;
		height: 200px;
		background: rgba(255, 114, 69, 1);
		box-shadow: 0 30px 10px -20px #ccc;
		border-radius: 30px;
	}

	.chart {
		display: flex;
		margin-top: 30px;
		.charts-box {
			width: 100px;
			height: 100px;
		}
	}
	
	.card-list{
		position: absolute;
		left: 0;
		width: 100%;
		height: calc(100% - 350px);
		background: rgba(255, 255, 255, 1);
		box-shadow: 0px -12px 22px rgba(0, 0, 0, 0.03);
		border-radius: 28px 28px 0 0;
	}
</style>
